.wrapper {
    background-color: rgb(62, 173, 164);
    width: 100%;
    height: 100%;
    margin: 0px 0px 0px 0px;
}

*p{
    margin: 0px 0px 0px 0px;
}

.title {
    color: white;
    font-size: 42px;
}

.subtitle {
    color: rgba(255, 255, 255, 0.514);
}

.posts_wrapper {
    display: flex;
    flex-direction: column;
    padding:20px;
    font-size: 20px;
}  

.posts_texts {
    display: flex;
    flex-direction: column;
    padding-left: 20px;
    padding-bottom: 10px;
}

.posts_content {
    display: flex;
    text-align: start;
}

.post_title {
    color: white;
}