a {
    text-decoration: none;
}


.ocean {
    width: 100%;
    height: 150px;
    background-color: rgb(36, 148, 110);
    position: fixed;
    bottom: 0%;
    opacity: 0.7;
    animation: swell 5s  infinite;
}

.wave_one {
    width: 100%;
    height: 150px;
    background-color: rgb(17, 126, 117);
    position: absolute;
    opacity: 0.6;
    bottom: 0%;
    animation: swell 5s  infinite;
}

.wave_two {
    width: 100%;
    height: 150px;
    opacity: 0.4;
    background-color: rgb(17, 95, 126);
    position: absolute;
    bottom: 0%;
    animation: swellTwo 5s  infinite;
}

.wrapper {
    width: 100%;
    height: 100vh;
    background-color: aquamarine;
    display: flex;
    align-items: center;
    justify-content: space-around;
    overflow-y: hidden;
    padding-top: 40px;
}




.item {
    animation: itemAnimationOut 1s linear  forwards;
    transition: 0.5s ease;
}

.item:hover {
    animation: itemAnimationIn 1s linear  infinite;
    transition: 0.5s ease;
}


.item_img {
    cursor: pointer;
}

.item_img img{
    display: block;
    width: 200px;
    height: 200px;
}

.item_link {
    text-decoration: none;
    color: white;
    font-size: 30px;
}

#djinni img{
    border-radius: 50%;
    width: 210px;
    
}

/* LOGO WORK.UA */

.blue {
    display: flex;
    width: 200px;
    border-radius: 100%;
    height: 200px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #00a1f1;
}

.white {
    width: 150px;
    height: 150px;
    background-color: white;
    border-radius: 100%;
    position: relative;
}

.red {
    width: 100px;
    height: 100px;
    background-color: #e42469;
    border-radius: 100%;
    position: absolute;
    left: 15%;
    top: 15%;
}


/* ANIMATION */

@keyframes swell {
    0%,
      100% {
      height: 150px;
      transform: translate3d(0, 10px, 0);
      border-top-right-radius: 70%;
      border-top-left-radius: 10%;
      clip-path: polygon(54% 0, 77% 15%, 100% 0, 100% 70%, 100% 100%, 50% 100%, 0 100%, 0 73%, 0 0, 24% 15%);
    }
    50% {
      height: 90px;
      transform: translate3d(0, 40px, 0);
      border-top-left-radius: 50%;
      border-top-right-radius: 5%;
      clip-path: polygon(54% 0, 78% 0, 100% 0, 100% 70%, 100% 100%, 50% 100%, 0 100%, 0 73%, 0 0, 23% 0);
    }
  }

  @keyframes swellTwo {
    0%,
      100% {
      transform: translate3d(0, 10px, 0);
      border-top-right-radius: 10%;
      border-top-left-radius: 70%;
      clip-path: polygon(54% 0, 77% 15%, 100% 0, 100% 70%, 100% 100%, 50% 100%, 0 100%, 0 73%, 0 0, 24% 15%);
    }
    50% {
      transform: translate3d(0, 40px, 0);
      border-top-left-radius: 5%;
      border-top-right-radius: 50%;
      clip-path: polygon(54% 0, 78% 0, 100% 0, 100% 70%, 100% 100%, 50% 100%, 0 100%, 0 73%, 0 0, 23% 0);
    }
  }

  @keyframes itemAnimationIn {
    0% , 100%{
        transform: scale(1.1);
        transition: 0.5s ease;
    }

    50% {
        transform: scale(1.2);
        transition: 0.5s ease;
    }
  }



/* MOBILE */

@media (min-width: 320px) {
    .wrapper {
        flex-wrap: wrap;
        padding-top: 120px;
    }  
}

@media (min-width: 375px) {
    .wrapper {
        flex-wrap: wrap;
        padding-top: 120px;
    }  
}

@media (min-width: 425px) {
    .wrapper {
        flex-direction: column;
        padding-top: 120px;
    }
    
}

@media (min-width: 1024px) {
    .wrapper {
        flex-direction: row;
    }
    
}