.wrapper {
    width: 100%;
    height: 500px;
    background-color: gray;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}


.text {
    display: hidden;
    height: 0px;
    color: rgb(255, 255, 255);
    opacity: 0;
    font-size: 20px;
    padding-top: 60px;
    padding-left: 20px;
    padding-right: 20px;
    transition: 1s ease;
}

.textActive {
    display: block;
    opacity: 1;
    color: rgb(255, 255, 255);
    transition: 1s ease;
    height: 300px;
}



.openButton {
    background-color: transparent;
    color: white;
    width: 150px;
    height: 150px;
    font-size: 20px;
    text-transform:uppercase ;
    border:2px white solid;
    cursor: pointer;
}

.openButton:focus {
    animation: rotate 2s forwards;
}


/* ANIMATION */

@keyframes rotate {
    0% , 100% {
        transform: perspective(400px) rotateY(0deg);
    }
    50% {
        transform: perspective(400px) rotateY(360deg);
    }
}