* {
    margin: 0px 0px 0px 0px;
    box-sizing: border-box;
}

.wrapper {
    width: 100%;
    height: auto;
    transform: translateY(-20px);
}



.navigation {
    display: flex;
    justify-content: end;
    width: 100%;
}


.blur {
    display: none;
    backdrop-filter: blur(3px);
    position: fixed;
    width: 100%;
    height: 100vh;
}

.activeBlur {
    display: block;
}



.links_wrapper {
    display: none;
    justify-content: center;
    align-items: center;
    width: 450px;
    flex-direction: column;
    background-color: black;
    position: relative;
    z-index: 150;
    transition: 0.5s ease;
    animation: backgroudAnimation 10s ease infinite;
}

.active {
    display: flex;
}

.button_wrapper {
    cursor: pointer;
    width: 40px;
    height: 40px;
    position: fixed;
    right: 10px;
    top: 20px;
    z-index: 200;
}

.navigation_button {
    display: block;
    background-color: white;
    width: 30px;
    height: 2px;
   
}
.navigation_button::before {
    display: block;
    content: '';
    background-color: white;
    width: 30px;
    height: 2px;
    top: 7px;
    position: absolute;
}
.navigation_button::after {
    display: block;
    content: '';
    background-color: white;
    width: 30px;
    height: 2px;
    bottom: 23px;
    position: absolute;
}

.navigation_link {
    width: 140px;
    margin: 15px;
    padding: 15px;
    text-decoration: none;
    color: white;
    font-size: 20px;
    transition: 0.5s ease;
}

.navigation_link:hover {
    transform: scale(1.2);
}

.navigation {
    display: flex;
    justify-content: end;
    width: 100%;
}

.navigation_link:hover {
    transform: translateY(-5px) scale(1.2);
    transition: 0.5s ease;
}

/* ANIMATION */

@keyframes backgroudAnimation {
    0% {
        background-color: black;
    }
    50% {
        background-color: rgb(45, 88, 95);
    }
    100% {
        background-color: black;
    }
}

/* MOBILE */

@media (min-width:320px) {
    .wrapper {
        position: fixed;
        left: 0%;
    }
}

@media (min-width:425px) {
    .wrapper {
        position: fixed;
        left: 0%;
    }
}

@media (min-width: 425px) {
    .navigation_link {
        padding-top: 20px;
        padding-bottom: 20px;
    }
}