.wrapper {
    width: 100%;
    height: 100%;
    background-color: rgb(161, 156, 156);
    font-family: 'Rubik', sans-serif;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 140px;
    padding-bottom: 250px;
}

.item {
    width: 300px;
    margin: 20px;
    padding: 10px;
    background-color: rgb(93, 93, 201);
    font-size: 20px;
    transform: skewY(15deg);
    display: flex;
    color: white;
    justify-content: center;
    cursor: pointer;
    transition: 0.5s ease;
    outline: none;
    border: 0px;
    margin-bottom: 0px;
}




.item_info {
    display: block;
    width: 300px;
    margin: 20px;
    margin-top: 0px;
    font-size: 0px;
    color: rgba(255, 255, 255, 0) ;
    transform: skewY(15deg);
    height: 0px;
    background-color: rgb(93, 93, 201);
    transition: 0.5s ease;
    margin-bottom: 20px;
}

.openDescription {
    display: block;
    font-size: 30px;
    padding: 20px 10px 20px 10px;
    background-color: rgb(19, 19, 65);
    color: white;
    height: 250px;
    transition: 0.5s ease;
    margin-bottom: 20px;
    position: relative;
}

.description_active {
    display: block;
    background-color: transparent;
    margin: 0 auto;
    color: white;
    font-size: 30px;
    margin-top: 30px;
    border: 2px white solid;
    transition: 0.3s ease;
}

.description_active:active {
    transform: scale(1.2);
    transition: 0.3s ease;
}

/* ANIMATION */

@keyframes rotate {
    0% {
        transform: skewY(15deg);
        transition: 0.5s ease;
    }
    100% {
        transform: skewY(0deg);
        transition: 0.5s ease;
    }
}

/* MOBILE */

@media (max-width: 728px) {
    .item {
        transform: skewY(0deg);
    }
    .item_info {
        transform: skewY(0deg);
    }
    .wrapper {
        justify-content: center;
        padding-top: 120px;
    }
}
