*{
    box-sizing: border-box;
}

.wrapper {
    width: 100%;
    height: 100vh;
    padding-top: 220px;
    padding-bottom: 240px;
    padding-left: 20px;
    padding-right: 20px;
    background-color: gray;
    display: flex;
    justify-content: center;
    align-items: center;
}



.avatar {
    display: block;
}

.avatar img {
    display: block;
    width: 250px;
    border-radius: 10px;
}

.info {
    color: white;
    font-size: 20px;
    padding-left: 20px;
    padding-right: 20px;
}

/* MOBILE */

@media (min-width:320px) {
    .wrapper{
        flex-direction: column;
    }
}