*{
    margin: 0px 0px 0px 0px;
}

.wrapper {
    width: 100%;
    height: 100%;
    background-color: black;
}

.content {
    width: 100%;
    padding-top: 270px;
    padding-bottom:270px ;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
}

.title {
    padding-bottom: 20px;
    font-size: 28px;
    text-transform: uppercase;
}

.text {
    font-size: 20px;
    text-align: center;
    width: 60%;
    padding-top: 20px;
}
