.wrapper {
    width: 100%;
    padding-top: 20px;
    padding-bottom: 20px;
    height: 120px;
    background-color: black;
    display: flex;
    justify-content: space-between;
    position: fixed;
    z-index: 200;
    padding-left: 20px;
    padding-right: 20px;
    animation: backgroudAnimation 10s ease infinite;
}


* {
    margin: 0px 0px 0px 0px;
    box-sizing: border-box;
}

.logo {
    color: white;
    font-size: 50px;
    border: 2px white solid;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 3px;
}

.logo_link {
    text-decoration: none;
}

/* ANIMATION */

@keyframes backgroudAnimation {
    0% {
        background-color: black;
    }
    50% {
        background-color: rgb(45, 88, 95);
    }
    100% {
        background-color: black;
    }
}

